import React, { useState } from "react";
import { Box, Dialog, DialogContent, Stack } from "@mui/material";
import {
  MobileDateTimePicker,
  LocalizationProvider,
  LoadingButton,
} from "@mui/lab";
import AdapterDateFns from "@mui/lab/AdapterDateFns";
import moment from "moment";

import { doModifyCampaign } from "../../../api/Campaign/Campaign";
import { useToastMessage } from "../../../contexts/ToastMessageContext";
import { useCampaign } from "../../../contexts/CampaignProvider";
import { CustomTextFieldFilled } from "../../../Components/UI/CustomTextField/CustomTextField";
import useStyles from "./style/muiStyles";

const ModifyCampaign = ({ open, closeDialog, campaignScheduleDate }) => {
  const classes = useStyles();
  const { setSuccessMessage, setErrorMessage } = useToastMessage();
  const { selectedCampaignId } = useCampaign();
  const [value, setValue] = useState(campaignScheduleDate.toDate());
  const [updating, setUpdating] = useState(false);
  const updateScheduleDate = async () => {
    try {
      setUpdating(true);
      const result = await doModifyCampaign(
        selectedCampaignId,
        value.getTime()
      );
      if (result.success) {
        setSuccessMessage("Successfully Updated Schedule Date");
      } else {
        throw new Error("Something went wrong");
      }
    } catch (error) {
      setErrorMessage(error.message);
    } finally {
      setUpdating(false);
      closeDialog();
    }
  };

  return (
    <Dialog
      open={open}
      onClose={closeDialog}
      maxWidth="md"
      className={classes["modify-campaign"]}
    >
      <DialogContent>
        <LocalizationProvider dateAdapter={AdapterDateFns}>
          <Stack spacing={3}>
            <h4>{"Modify Scheduled Date"}</h4>
            <MobileDateTimePicker
              value={value}
              onChange={(newValue) => {
                setValue(newValue);
              }}
              minDate={moment().toDate()}
              renderInput={(params) => (
                <CustomTextFieldFilled
                  {...params}
                  label="Scheduled At"
                  placeholder="Set Scheduled At"
                />
              )}
            />
            <Box
              style={{
                display: "flex",
                justifyContent: "flex-end",
              }}
            >
              <LoadingButton
                loading={updating}
                variant="contained"
                type="button"
                className="btn-update"
                onClick={updateScheduleDate}
              >
                {"Update"}
              </LoadingButton>
            </Box>
          </Stack>
        </LocalizationProvider>
      </DialogContent>
    </Dialog>
  );
};

export default ModifyCampaign;
