import React, { useCallback, useEffect, useState } from "react";
import { Button, Drawer, IconButton, Link, MenuItem } from "@mui/material";
import AdapterDateFns from "@mui/lab/AdapterDateFns";
import { DateTimePicker, LocalizationProvider } from "@mui/lab";
import moment from "moment";

import {
  delete_file,
  doCreateNewCampaign,
  getAllTemplates,
  getTemplateJSON,
  getTemplateSampleFile,
} from "../../../api/Campaign/Campaign";
import {
  CustomInputField,
  CustomTextFieldFilled,
} from "../../../Components/UI/CustomTextField/CustomTextField";
// Toast Message Context
import { useToastMessage } from "../../../contexts/ToastMessageContext";
import { useCampaign } from "../../../contexts/CampaignProvider";
import FileAttachment from "../../../Components/UI/FileAttachment";

import leftArrow from "../../../assets/icons/leftArrow.svg";

import downLoadIcon from "../assets/download.svg";
// styles
import useStyles from "../style/muiStyles";

const NewCampaign = ({ anchor = "right", open, onClose, doReload }) => {
  const classes = useStyles();
  const [creating, setCreating] = useState(false);
  const [campaignName, setCampaignName] = useState("");
  const [campaignType, setCampaignType] = useState("");
  const [campaignDetails, setCampaignDetails] = useState("");
  const [campaignTemplate, setCampaignTemplate] = useState("");
  const [dateTime, setDateTime] = useState(null);
  const [userData, setUserData] = useState(null);
  const [templates, setTemplates] = useState([]);
  const { setSelectedCampaignId, setLoad, setSelectedCampaignProgress } =
    useCampaign();
  const { setSuccessMessage, setErrorMessage } = useToastMessage();
  const dateTimeChange = (newValue) => {
    setDateTime(newValue);
  };
  const getTemplatesData = useCallback(async () => {
    try {
      if (!open) return;

      const templatesData = await getAllTemplates();
      if (templatesData.payload) {
        setTemplates(
          templatesData.payload.filter(
            (template) => template.template_status === "APPROVED"
          )
        );
      }
    } catch (error) {
      console.error(error);
    }
  }, [open]);

  const downloadSampleFormatFile = async () => {
    try {
      const template = templates
        .filter(({ id }) => campaignTemplate === id)
        .pop();
      const result = await getTemplateJSON(template.id);
      if (!result.success) return;
      const response = await getTemplateSampleFile({
        data: result.payload.data,
        name: result.payload.name,
      });
      const blob = await response.blob();
      await delete_file(`${result.payload.name}.xlsx`);
      // Create blob link to download
      const url = window.URL.createObjectURL(new Blob([blob]));
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download", `${result.payload.name}.xlsx`);
      // Append to html link element page
      document.body.appendChild(link);
      // Start download
      link.click();
      // Clean up and remove the link
      link.parentNode.removeChild(link);
      setSuccessMessage("Sample File Downloaded");
    } catch (error) {
      setErrorMessage(error.message);
    }
  };

  const createNewCampaign = async (e) => {
    const NewCampaignForm = e.target;
    try {
      e.preventDefault();
      if (campaignName === "") {
        throw new Error("Enter Campaign Name");
      }
      if (campaignType === "") {
        throw new Error("Select Campaign Type");
      }
      if (campaignDetails === "") {
        throw new Error("Enter Campaign Details");
      }
      if (campaignTemplate === "") {
        throw new Error("Select Campaign Template");
      }
      if (campaignType === "UPLOAD") {
        if (dateTime === null) {
          throw new Error("Validate all fields");
        }
        if (moment.duration(moment(dateTime).diff(moment())).asMinutes() < 15) {
          throw new Error(
            "Schedule time must be 15 mins after the current time."
          );
        }
        if (userData === null) {
          throw new Error("Validate all fields");
        }
      }
      setCreating(true);
      setSelectedCampaignId("");
      let formData = new FormData();
      formData.append("name", campaignName);
      formData.append("type", campaignType);
      formData.append("wa_template_id", campaignTemplate);
      formData.append("details", campaignDetails);
      formData.append("platform", "WHATSAPP");

      if (campaignType === "UPLOAD") {
        formData.append("schedule_date", dateTime.getTime());
        formData.append("file", userData, userData.name);
      }
      const newCampaignData = await doCreateNewCampaign(formData);
      if (!newCampaignData.success) {
        throw new Error(newCampaignData.message);
      } else {
        const payload = newCampaignData.payload;
        setSuccessMessage("Campaign Created Successfully");
        doReload((prev) => !prev);
        onClose();
        setSelectedCampaignId(payload.id);
        setSelectedCampaignProgress(payload.progress);
        setLoad(true);
        NewCampaignForm.reset();
        setCampaignName("");
        setCampaignType("");
        setCampaignDetails("");
        setCampaignTemplate("");
        if (dateTime !== null) setDateTime(null);
        if (userData !== null) setUserData(null);
      }
    } catch (error) {
      setErrorMessage(error.message);
    } finally {
      setCreating(false);
    }
  };

  useEffect(() => {
    getTemplatesData();
  }, [getTemplatesData]);

  return (
    <Drawer
      anchor="right"
      open={open}
      onClose={(ev, reason) =>
        !(reason === "backdropClick" || reason === "escapeKeyDown") && onClose()
      }
      className={classes["new-campaign"]}
    >
      {open && (
        <div className="wrapper">
          <div className="header">
            <IconButton
              aria-label="Back"
              className="back-btn"
              onClick={() => {
                onClose();
              }}
              style={{ color: "transparent", padding: 0 }}
            >
              <img
                alt="back"
                src={leftArrow}
                style={{ margin: "7.5px 10px" }}
              />
            </IconButton>
            <h1 className="title">{"Create Campaign"}</h1>
          </div>
          <form onSubmit={createNewCampaign}>
            <h3 className="form-title">{"Campaign Details"}</h3>
            <div className="field">
              <CustomTextFieldFilled
                type="text"
                label="Campaign Name"
                placeholder="Campaign Name"
                value={campaignName}
                onChange={({ target: { value } }) => setCampaignName(value)}
              />
            </div>
            <div className="field">
              <CustomTextFieldFilled
                select
                label="Campaign Type"
                placeholder="Campaign Type"
                value={campaignType}
                onChange={({ target: { value } }) => {
                  setCampaignType(value);
                }}
                variant="filled"
                type="select"
                inputProps={{ "aria-label": "Without label" }}
                fullWidth
              >
                {[
                  { label: "API Based ", value: "API" },
                  { label: "Uploaded Data ", value: "UPLOAD" },
                ].map(({ label, value }, index) => (
                  <MenuItem key={index} value={value}>
                    {label}
                  </MenuItem>
                ))}
              </CustomTextFieldFilled>
            </div>
            <div className="field">
              <CustomInputField
                multiline
                placeholder="Campaign Details"
                rows={5}
                value={campaignDetails}
                onChange={({ target: { value } }) => setCampaignDetails(value)}
              />
            </div>
            <div className="field">
              <CustomTextFieldFilled
                select
                label="Choose Template"
                placeholder="Choose Template"
                value={campaignTemplate}
                onChange={({ target: { value } }) => setCampaignTemplate(value)}
                variant="filled"
                type="select"
                inputProps={{ "aria-label": "Without label" }}
                fullWidth
              >
                {templates.map(({ name, id }, index) => (
                  <MenuItem key={index} value={id}>
                    {name}
                  </MenuItem>
                ))}
              </CustomTextFieldFilled>
            </div>
            {campaignType === "UPLOAD" && (
              <div className="field">
                <LocalizationProvider dateAdapter={AdapterDateFns}>
                  <DateTimePicker
                    label="Schedule Date & Time"
                    placeholder="Schedule Date & Time"
                    renderInput={(props) => (
                      <CustomTextFieldFilled
                        type="text"
                        name="scheduleOn"
                        {...props}
                      />
                    )}
                    value={dateTime}
                    onChange={dateTimeChange}
                  />
                </LocalizationProvider>
              </div>
            )}
            {campaignType === "UPLOAD" && (
              <div className="field">
                <h4 className="field-title">{"Uploaded Data"}</h4>
                <div className="sample">
                  <Link
                    component="button"
                    className="download"
                    underline="none"
                    type="button"
                    onClick={() => {
                      downloadSampleFormatFile();
                    }}
                    disabled={campaignTemplate === ""}
                  >
                    <span className="icon">
                      <img src={downLoadIcon} alt="down" className="icon" />
                    </span>
                    <span className="label">{"Download sample format"}</span>
                  </Link>
                </div>
                <FileAttachment
                  placeholder={`Upload User Data`}
                  value={userData}
                  removeAttachment={() => {
                    setUserData(null);
                  }}
                  inputChangedHandler={({
                    target,
                    target: {
                      files: [selectedFile],
                    },
                  }) => {
                    setUserData(selectedFile);
                    target.files = new DataTransfer().files;
                  }}
                  name="file"
                  width={"100%"}
                  height={"112px"}
                  allowedTypes={"XLS or XLSX Format"}
                  accept={".xlsx"}
                />
              </div>
            )}
            <div className="actions">
              <Button variant="contained" type="submit" disabled={creating}>
                {"Save"}
              </Button>
            </div>
          </form>
        </div>
      )}
    </Drawer>
  );
};

export default NewCampaign;
