import React from "react";
import { Button } from "@mui/material";

import { useCampaign } from "../../../contexts/CampaignProvider";

import bottomImage from "../assets/createorselect.svg";

const SelectOrCreateNew = () => {
  const { toggleCreateNew } = useCampaign();

  return (
    <div className="create-or-new">
      <div className="top">
        <div className="info">
          <h3 className="heading">{"Click on a campaign to see details."}</h3>
          <h3 className="heading">{"or"}</h3>
          <h3 className="heading">{"Create a new campaign"}</h3>
        </div>
        <Button
          type="button"
          variant="contained"
          onClick={() => {
            toggleCreateNew(true);
          }}
        >
          {"New Campaign"}
        </Button>
      </div>
      <div className="bottom">
        <div className="image">
          <img src={bottomImage} alt="Create or Select" />
        </div>
      </div>
    </div>
  );
};

export default SelectOrCreateNew;
