import * as React from "react";
import moment from "moment";

import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import * as actions from "../../../Store/actions/index";
import TextField from "@mui/material/TextField";
import { DatePicker, LocalizationProvider } from "@mui/lab/";

import AdapterDateFns from "@mui/lab/AdapterDateFns";
import { useDailog } from "../../../contexts/DailogProvider";
import {
  delete_file,
  downloadCampaignReport,
  download_file,
} from "../../../api/Campaign/Campaign";
import { useCampaign } from "../../../contexts/CampaignProvider";
import { connect } from "react-redux";
import { useWaitingLoader } from "../../../contexts/waitingLoaderContext";
import Loader from "../../../shared-components/loader";
import { useToastMessage } from "../../../contexts";
function ResponsiveDialog(props) {
  const [value, setValue] = React.useState(
    moment(new Date()).format("YYYY-MM")
  );
  const { open, handleClose } = useDailog();
  const { openLoader, closeLoader } = useWaitingLoader();
  const { selectedCampaignId, selectedCampaignName } = useCampaign();
  const { setSuccessMessage, setErrorMessage } = useToastMessage();

  const download = (month) => {
    if (!month) {
      return setErrorMessage("Select Month");
    }
    openLoader();
    const date = new Date(month);
    const finalMonth = `${date.getFullYear()}-${date.getMonth() + 1}`;
    downloadCampaignReport(finalMonth, selectedCampaignId)
      .then(async (response) => {
        try {
          const blob = await response.blob();
          if (blob.type.length <= 1) {
            // Create blob link to download
            const url = window.URL.createObjectURL(new Blob([blob]));
            const link = document.createElement("a");
            link.href = url;
            link.setAttribute(
              "download",
              `${selectedCampaignName}_${date.toLocaleString("en-us", {
                month: "short",
              })}_${date.getFullYear()}.pdf`
            );
            // Append to html link element page
            document.body.appendChild(link);
            // Start download
            link.click();
            // Clean up and remove the link
            link.parentNode.removeChild(link);
            setSuccessMessage("Report Downloaded");
          } else {
            throw new Error("No data found for selected month.");
          }
        } catch (err) {
          setErrorMessage(err.message);
        }
      })
      .catch((err) => {
        console.log("error", err);
      })
      .finally(() => {
        closeLoader();
      });
  };

  return (
    <div>
      <Dialog
        // fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">Select Month</DialogTitle>
        <DialogContent>
          <LocalizationProvider dateAdapter={AdapterDateFns}>
            <DatePicker
              views={["year", "month"]}
              minDate={new Date("2001-03-01")}
              maxDate={new Date("2029-06-01")}
              value={value}
              style={{ margin: "10px", width: "30%" }}
              onChange={(newValue) => {
                setValue(newValue);
              }}
              renderInput={(params) => (
                <TextField {...params} helperText={null} />
              )}
            />
          </LocalizationProvider>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={async () => {
              await download(value);
              handleClose();
            }}
          >
            Download
          </Button>
        </DialogActions>
      </Dialog>
      <Loader label="Downloading Report" />
    </div>
  );
}
const mapStateToProps = (state) => {
  return {
    user: state.user.user,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    startLoading: () => dispatch(actions.startLoading()),
    cancelLoading: () => dispatch(actions.cancelLoading()),
    error: (err) => dispatch(actions.error(err)),
    fetchUserDetail: () => dispatch(actions.fetchUserDetail()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ResponsiveDialog);
