import axios from "axios";
import { API, API_V2 } from "../../Backend";
import decryptResponse from "../../Shared/decryptResponse";
import { AuthenticatedHeaders } from "../../utils/ApiHeaders";

export const doCreateNewCampaign = async (reqData) => {
  try {
    const res = await axios({
      url: `${API_V2}/campaigns`,
      method: "POST",
      headers: AuthenticatedHeaders(),
      data: reqData,
    });

    return res.data;
  } catch (err) {
    return {
      message: err.response.data.errors
        ? err.response.data.errors[0].field === "schedule_date"
          ? "Schedule time must be 15 mins after the current time."
          : err.message
        : err.message,
    };
  }
};

export const updateScheduleDate = async (campaignId) => {
  try {
    const res = await fetch(`${API_V2}/campaigns/schedule-date/${campaignId}`, {
      method: "POST",
      headers: AuthenticatedHeaders(),
      body: JSON.stringify({ scheduleDate: "1679478155962" }),
    });
    return await res.json();
  } catch (err) {
    return {
      message: err.message,
    };
  }
};

