/* eslint-disable react-hooks/exhaustive-deps */
import React, {
  Fragment,
  useCallback,
  useEffect,
  useRef,
  useState,
} from "react";
import {
  Button,
  Chip,
  Link,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  FilledInput,
  InputAdornment,
  Skeleton,
  Stack,
} from "@mui/material";
import { LoadingButton } from "@mui/lab";
import { ContentCopy, Edit, Pause, PlayArrow } from "@mui/icons-material";
import moment from "moment";
import { useToastMessage } from "../../../contexts/ToastMessageContext";
import { getTemplatePreview } from "../../../api/WhatsappTemplate";
import {
  getCampaignsById,
  updateCampaignProgress,
} from "../../../api/Campaign/Campaign";
import { useCampaign } from "../../../contexts/CampaignProvider";
import { useDailog } from "../../../contexts/DailogProvider";
import ResponsiveDialog from "../../../Components/CampaignDashboard/UI/dailog";

import Preview from "./Preview";
import downLoadIcon from "../assets/download.svg";
import linkIcon from "../assets/link.svg";
import deliveredIcon from "../assets/delivered.svg";
import failedIcon from "../assets/failed.svg";
import readIcon from "../assets/read.svg";
import repliedIcon from "../assets/replied.svg";
import replyRateIcon from "../assets/reply-rate.svg";
import sentIcon from "../assets/sent.svg";
import ModifyCampaign from "../ModifyCampaign";

const CampaignDetail = () => {
  const [totalSent, setTotalSent] = useState();
  const [delivered, setDelivered] = useState();
  const [failed, setFailed] = useState();
  const [read, setRead] = useState();
  const [replied, setReplied] = useState();
  const [responseRate, setResponseRate] = useState();
  const [campaignFileUrl, setCampaignFileUrl] = useState();
  const [campaignScheduleDate, setCampaignScheduleDate] = useState();
  const [totalRows, setTotalRows] = useState();
  const [campaignToggling, setCampaignToggling] = useState(false);
  const [modifyCampaign, setModifyCampaign] = useState(false);
  const [campaignStatus, setCampaignStatus] = useState(true);
  const apiUrlRef = useRef();
  const [apiUrl, setAipUrl] = useState();
  const [templateId, setTemplateId] = useState();
  const [previewData, setPreviewData] = useState();
  const [
    avgReplyRate,
    // setAverageReplies
  ] = useState(31);
  const { handleClickOpen } = useDailog();
  const { setSuccessMessage, setErrorMessage } = useToastMessage();
  const {
    load,
    selectedCampaignId,
    selectedCampaignProgress,
    campaignDescription,
    setCampaignDescription,
    selectedCampaignName,
    setSelectedCampaignName,
    setSelectedCampaignProgress,
    setLoad,
    campaignType,
    setCampaignType,
  } = useCampaign();

  const getStatusColor = (status) => {
    if (status === "ACTIVE") {
      return "#FFBA43";
    }
    if (status === "PAUSED") {
      return "#EE9A9A";
    }
    if (status === "COMPLETED") {
      return "#12B347";
    }
  };

  const GridItem = ({ title, icon, value, unit }) => {
    return (
      <div className="report-item">
        <div className="top">
          <h3 className="item-title">{title}</h3>
          <img src={icon} alt="icon" />
        </div>
        <div className="bottom">
          {load ? (
            <Skeleton className="item-value" animation="wave" />
          ) : (
            <h3 className="item-value">
              {title === "Reply Rate"
                ? value > 0
                  ? `${value}%`
                  : "N.A."
                : Intl.NumberFormat("en-US").format(value)}
            </h3>
          )}
          {load ? (
            <Skeleton className="item-unit" animation="wave" />
          ) : (
            <h4 className="item-unit">
              {title === "Reply Rate" ? (value > 0 ? unit : "") : unit}
            </h4>
          )}
        </div>
      </div>
    );
  };

  const getCampaign = useCallback(async () => {
    try {
      if (selectedCampaignId) {
        const res = await getCampaignsById(selectedCampaignId);
        const campaignData = res.payload;
        if (res.success) {
          setCampaignStatus(campaignData.progress === "ACTIVE" ? true : false);
          setSelectedCampaignProgress(campaignData.progress);
          setSelectedCampaignName(campaignData.name);
          setCampaignDescription(campaignData.details);
          setTemplateId(campaignData.wa_template_id);
          setCampaignType(campaignData.type);
          if (campaignData.type === "UPLOAD") {
            setCampaignScheduleDate(moment(campaignData.schedule_date));
            setCampaignFileUrl(campaignData.file_url);
            setTotalRows(campaignData.total_rows);
          }
          setAipUrl(campaignData.simple_url);
          setReplied(campaignData.statistics.responded);
          setTotalSent(campaignData.statistics.sent);
          setDelivered(campaignData.statistics.delivered);
          setFailed(campaignData.statistics.failed);
          setRead(campaignData.statistics.read);
          setResponseRate(campaignData.statistics.response_rate);
          setLoad(false);
        }
      }
    } catch (error) {
      console.error(error);
      setErrorMessage(error.message);
    }
  }, [selectedCampaignId]);

  const getPreviewData = useCallback(async () => {
    if (templateId) {
      const res = await getTemplatePreview(templateId);
      if (!res.success) {
        return setErrorMessage(res.message);
      } else {
        setPreviewData(res.payload);
      }
    }
  }, [templateId]);

  useEffect(() => {
    getCampaign();
  }, [getCampaign]);

  useEffect(() => {
    getPreviewData();
  }, [getPreviewData]);

  return (
    <div className="detail">
      <div className="wrapper">
        <div className="head">
          <div className="left">
            <h3 className="heading">
              {load ? (
                <Skeleton animation="wave" width="40%" />
              ) : (
                selectedCampaignName
              )}
            </h3>
            <p className="sub-heading">
              {load ? (
                <Skeleton animation="wave" width="60%" />
              ) : (
                campaignDescription
              )}
            </p>
          </div>
          <div className="right">
            {load ? (
              <Skeleton
                variant="rectangular"
                animation="wave"
                width={53}
                height={16}
                sx={{ borderRadius: 16 }}
              />
            ) : (
              <Chip
                label={selectedCampaignProgress}
                className="badge"
                size="small"
                style={{
                  backgroundColor: getStatusColor(selectedCampaignProgress),
                  alignItems: "center",
                  justifyContent: "center",
                  width: "auto",
                }}
              />
            )}
          </div>
        </div>
        <div className="content">
          {!load && selectedCampaignProgress !== "COMPLETED" && (
            <div className="actions">
              <LoadingButton
                loading={campaignToggling}
                color="secondary"
                onClick={async () => {
                  try {
                    setCampaignToggling(true);
                    let result = await updateCampaignProgress(
                      selectedCampaignId,
                      selectedCampaignProgress === "ACTIVE"
                        ? "PAUSED"
                        : "ACTIVE"
                    );
                    if (result.message) {
                      setCampaignStatus(!campaignStatus);
                      setSelectedCampaignProgress(
                        selectedCampaignProgress === "ACTIVE"
                          ? "PAUSED"
                          : "ACTIVE"
                      );
                      setSuccessMessage("Campaign Progress Updated");
                    }
                  } catch (err) {
                    setErrorMessage(err.message);
                  } finally {
                    setCampaignToggling(false);
                  }
                }}
                className={campaignStatus ? "" : "btn-resume"}
                variant="contained"
                startIcon={campaignStatus ? <Pause /> : <PlayArrow />}
              >
                {`${campaignStatus ? "Pause" : "Resume"} Campaign`}
              </LoadingButton>
              {campaignType !== "API" && (
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => setModifyCampaign(true)}
                  startIcon={<Edit />}
                >
                  {"Modify Campaign"}
                </Button>
              )}
            </div>
          )}
          <div className="detail-grid">
            <div className="grid-head">
              <h4 className="title">{"Statistics"}</h4>

              <Link
                component="button"
                onClick={handleClickOpen}
                underline="none"
                className="download"
                disabled={load}
              >
                <img src={downLoadIcon} alt="down" className="icon" />
                <span className="label">{"Download Report"}</span>
              </Link>
            </div>
            <ResponsiveDialog />
            <div className="inner">
              {[
                {
                  title: "Sent",
                  icon: sentIcon,
                  value: totalSent,
                  unit: "messages",
                },
                {
                  title: "Replied",
                  icon: repliedIcon,
                  value: replied,
                  unit: "messages",
                },
                {
                  title: "Reply Rate",
                  icon: replyRateIcon,
                  value: responseRate,
                  unit: `${
                    responseRate < avgReplyRate ? "lower" : "higher"
                  } than avg. ${avgReplyRate}%`,
                },
                {
                  title: "Delivered",
                  icon: deliveredIcon,
                  value: delivered,
                  unit: "messages",
                },
                {
                  title: "Read",
                  icon: readIcon,
                  value: read,
                  unit: "messages",
                },
                {
                  title: "Failed",
                  icon: failedIcon,
                  value: failed,
                  unit: "messages",
                },
              ].map((data, index) => (
                <GridItem key={index} {...data} />
              ))}
            </div>
          </div>
          <div className="linked-template">
            <h4 className="title">{"Linked Template"}</h4>
            <div className="template">
              <span className="name">
                {load ? (
                  <Skeleton animation="wave" variant="text" width="70px" />
                ) : (
                  previewData?.name
                )}
              </span>
              {previewData && (
                <Link
                  href={`/business/wa-template-messages?previewTemplate=${previewData?.id}`}
                  target="_blank"
                  underline="none"
                  className="download"
                >
                  <img src={linkIcon} alt="link" />
                </Link>
              )}
            </div>
          </div>
          {campaignType === "API" && (
            <div className="api">
              <h4 className="title">{"API"}</h4>
              {load ? (
                <Skeleton
                  animation="wave"
                  variant="rectangular"
                  sx={{
                    backgroundColor: "#12B0EE1A",
                    borderRadius: 2,
                    height: 40,
                  }}
                />
              ) : (
                <FilledInput
                  value={apiUrl}
                  inputProps={{
                    readOnly: true,
                    ref: apiUrlRef,
                  }}
                  endAdornment={
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={() => {
                          apiUrlRef.current.select();
                          document.execCommand("Copy");
                          setSuccessMessage("API copied to your clipboard");
                        }}
                        edge="end"
                        // disableRipple={true}
                        // disableFocusRipple={true}
                      >
                        <ContentCopy />
                      </IconButton>
                    </InputAdornment>
                  }
                />
              )}
            </div>
          )}
          {false && (
            <div className="linked-users">
              <h4 className="title">{"36929 Users Selected"}</h4>
              <p className="name">{"5 Filters Applied"}</p>
              <TableContainer
                sx={{
                  border: "1px solid rgba(0, 0, 0, 0.1)",
                  borderRadius: "10px",
                  margin: "40px 0",
                }}
              >
                <Table aria-label="Monthly Report" className="reports">
                  <TableHead>
                    <TableRow
                      sx={{ borderBottom: "1px solid rgba(0, 0, 0, 0.1)" }}
                    >
                      <TableCell
                        sx={{ borderRight: "1px solid rgba(0, 0, 0, 0.1)" }}
                      >
                        {"Parameter"}
                      </TableCell>
                      <TableCell width={"70%"}>{"Filtered Value"}</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    <TableRow
                      sx={{ borderBottom: "1px solid rgba(0, 0, 0, 0.1)" }}
                    >
                      <TableCell
                        sx={{
                          borderRight: "1px solid rgba(0, 0, 0, 0.1)",
                          borderBottom: "none",
                        }}
                      >
                        {"Lead Date"}
                      </TableCell>
                      <TableCell
                        sx={{
                          borderBottom: "none",
                        }}
                      >
                        {"1st Sep 21 to 30th Oct 21"}
                      </TableCell>
                    </TableRow>
                    <TableRow
                      sx={{ borderBottom: "1px solid rgba(0, 0, 0, 0.1)" }}
                    >
                      <TableCell
                        sx={{
                          borderRight: "1px solid rgba(0, 0, 0, 0.1)",
                          borderBottom: "none",
                        }}
                      >
                        {"Bike"}
                      </TableCell>
                      <TableCell
                        sx={{
                          borderBottom: "none",
                        }}
                      >
                        {"Honda Shine"}
                      </TableCell>
                    </TableRow>
                    <TableRow
                      sx={{ borderBottom: "1px solid rgba(0, 0, 0, 0.1)" }}
                    >
                      <TableCell
                        sx={{
                          borderRight: "1px solid rgba(0, 0, 0, 0.1)",
                          borderBottom: "none",
                        }}
                      >
                        {"Intent"}
                      </TableCell>
                      <TableCell
                        sx={{
                          borderBottom: "none",
                        }}
                      >
                        {"under 3 months"}
                      </TableCell>
                    </TableRow>
                    <TableRow
                      sx={{ borderBottom: "1px solid rgba(0, 0, 0, 0.1)" }}
                    >
                      <TableCell
                        sx={{
                          borderRight: "1px solid rgba(0, 0, 0, 0.1)",
                          borderBottom: "none",
                        }}
                      >
                        {"Region"}
                      </TableCell>
                      <TableCell
                        sx={{
                          borderBottom: "none",
                        }}
                      >
                        {"South"}
                      </TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell
                        sx={{
                          borderRight: "1px solid rgba(0, 0, 0, 0.1)",
                          borderBottom: "none",
                        }}
                      >
                        {"Assigned"}
                      </TableCell>
                      <TableCell
                        sx={{
                          borderBottom: "none",
                        }}
                      >
                        {"False"}
                      </TableCell>
                    </TableRow>
                  </TableBody>
                </Table>
              </TableContainer>
            </div>
          )}
          {campaignType === "UPLOAD" && campaignFileUrl && totalRows && (
            <div className="users">
              <h4 className="title">{`${totalRows} Users Selected`}</h4>
              <Link
                href={campaignFileUrl}
                underline="none"
                className="download"
                download={true}
              >
                {load ? (
                  <Skeleton animation="wave" variant="text" width="150px" />
                ) : (
                  <Fragment>
                    <img src={downLoadIcon} alt="down" className="icon" />
                    <span className="label">{"Download User Data"}</span>
                  </Fragment>
                )}
              </Link>
            </div>
          )}
          {campaignType === "UPLOAD" && campaignScheduleDate && (
            <div className="schedule">
              <h4 className="title">{"Scheduled At"}</h4>
              <p className="at">
                {load ? (
                  <Skeleton animation="wave" variant="text" width="150px" />
                ) : (
                  campaignScheduleDate.format("Do MMMM YYYY [at] h:mmA")
                )}
              </p>
            </div>
          )}
        </div>
      </div>
      <div className="preview">
        <div className="header">
          <h3 className="heading">{"Preview Message"}</h3>
        </div>
        {load ? (
          <div className="preview-content">
            <div className="preview-card">
              <Stack spacing={0.7}>
                <Skeleton variant="rectangular" width="100%" height={130} />
                <Skeleton />
                <Skeleton width="60%" />
              </Stack>
            </div>
          </div>
        ) : (
          previewData && <Preview previewData={previewData} />
        )}
      </div>
      {campaignScheduleDate && (
        <ModifyCampaign
          {...{
            open: modifyCampaign,
            closeDialog: () => setModifyCampaign(false),
            campaignScheduleDate,
          }}
        />
      )}
    </div>
  );
};

export default CampaignDetail;
