import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  "modify-campaign": {
    "& .MuiDialog-container": {
      "& .MuiDialog-paper": {
        margin: "6px",

        "& .MuiDialogContent-root": {
          padding: "20px",

          "& .btn-update": {
            alignItems: "center",
            backgroundColor: "#12B0EE",
            borderRadius: "8px",
            boxShadow: "none",
            fontSize: "15px",
            fontWeight: 500,
            height: "43px",
            lineHeight: "18px",
            textTransform: "capitalize",
          },
        },
      },
    },
  },

  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {
    "modify-campaign": {
      "& .MuiDialog-container": {
        "& .MuiDialog-paper": {
          margin: "32px",

          "& .MuiDialogContent-root": {
            padding: "20px 24px",
          },
        },
      },
    },
  },
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
}));

export default useStyles;
