import React from "react";
import { Hidden } from "@mui/material";

import { useCampaign } from "../../contexts/CampaignProvider";

import CampaignListHolder from "./List";
import CampaignDetail from "./Detail";
import SelectOrCreateNew from "./SelectOrCreateNew";

// styles
import useStyles from "./style/muiStyles";

const CampaignContainer = () => {
  const classes = useStyles();
  const { selectedCampaignId } = useCampaign();

  return (
    <div className={classes["container"]}>
      <div className="inner-wrapper">
        <CampaignListHolder />
        {selectedCampaignId && <CampaignDetail />}
        <Hidden xsDown implementation="js">
          {selectedCampaignId === "" && <SelectOrCreateNew />}
        </Hidden>
      </div>
    </div>
  );
};

export default CampaignContainer;
