import React, { useState, useEffect, useRef } from "react";
import Sidebar from "../../Components/Sidebar/Sidebar";
import { useBusiness } from "../../contexts/BusinessProvider";
import classes from "./Campaign.module.css";
import ICampaignProps from "./CampaignProps";
import CampaignContainer from "../../Container/Campaign";
import { Hidden } from "@mui/material";
import NavbarContainer from "../../shared-components/mobile/NavbarContainer/NavbarContainer";
import BottomNavigation from "../../shared-components/mobile/BottomNavigation/BottomNavigation";
import CampaignMobileList from "../../Container/Campaign/MobileList";
import CampaignMobileDetail from "../../Container/Campaign/CampaignMobileDetail";
import { useCampaign } from "../../contexts/CampaignProvider";

const Campaign: React.FunctionComponent<ICampaignProps> = ({
  location,
  history,
}) => {
  const {
    switchBusiness,
    selectedBusiness,
    allBusiness,
    getAllUserBusiness,
    fetchDefaultBusiness,
  } = useBusiness();
  const { selectedCampaignId } = useCampaign();
  const [states, setStates] = useState({
    toggled: location.hash === "#open" ? true : false,
    selectedLead: null,
    toggle_DropDown: false,
  });
  const switchBetweenBusiness = (e: any) => {
    if (selectedBusiness.id === e.target.value) return;

    const business = allBusiness.find(
      (data: any) => data.businessId === e.target.value
    );

    if (business) {
      switchBusiness(e.target.value, business);

      window.location.href = "/campaign";
    }
  };

  const toggleBar = () => {
    if (states.toggled) {
      history.push("#close");
    }
    if (!states.toggled) {
      history.push("#open");
    }
    return setStates({ ...states, toggled: !states.toggled });
  };

  const loadDataRef = useRef(() => {
    !selectedBusiness && fetchDefaultBusiness();
    allBusiness.length < 1 && getAllUserBusiness();
  });

  useEffect(() => {
    loadDataRef.current();
  }, []);

  return (
    <React.Fragment>
      <Hidden smUp implementation="js">
        <NavbarContainer
          toggleBar={toggleBar}
          open={states.toggled}
          switchBusiness={switchBusiness}
          isLoading={false}
        />
        {!selectedCampaignId && <CampaignMobileList />}
        {selectedCampaignId && <CampaignMobileDetail />}
        <BottomNavigation />
      </Hidden>
      <Hidden xsDown implementation="js" only="xs">
        <div className={classes.Container}>
          <div className={classes.CampaignContent}>
            <Sidebar
              switchBetweenBusiness={switchBetweenBusiness}
              location={location}
              selectedBusiness={selectedBusiness}
              business={allBusiness}
              open={states.toggled}
              handleClose={toggleBar}
              className={classes.Sidebar}
            />
            <CampaignContainer />
          </div>
        </div>
      </Hidden>
    </React.Fragment>
  );
};

export default Campaign;
