import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  "mobile-list": {
    borderRight: "1px solid rgba(0, 0, 0, 0.05)",
    display: "flex",
    flexDirection: "column",
    flex: "0 1 100%",
    height: "calc(100% - 69px)",
    width: "100%",

    "& .head": {
      alignItems: "center",
      boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.1)",
      display: "flex",
      padding: "10px 20px",
      position: "relative",

      "& .title-area": {
        alignItems: "center",
        display: "flex",
        gap: "0 16px",

        "& .title": {
          color: "#24272C",
          fontSize: "20px",
          fontWeight: 700,
          lineHeight: "23px",
        },

        "& .filter": {
          alignItems: "center",
          color: "#949598",
          fontSize: "14px",
          fontWeight: 400,
          lineHeight: "24px",
          textTransform: "capitalize",
          padding: "4px 0",
        },
      },

      "& .search": {
        display: "flex",
        flexGrow: 1,

        "& .search-input": {
          border: "1px solid #24272C80",
          borderRadius: "8px",
          flexGrow: 1,
          margin: 0,
          padding: "0 12px",
          outline: "none",
        },
      },

      "& .MuiIconButton-root": {
        backgroundColor: "transparent",
        padding: "4px",
        maxHeight: "32px",

        "&.btn-search": {
          marginLeft: "auto",
        },

        "&:hover": {
          backgroundColor: "transparent",
        },
      },
    },

    "& .campaign": {
      display: "flex",
      flex: "1 0 0",
      flexDirection: "column",
      paddingBottom: "56px",

      "& .MuiList-root": {
        flex: "1 0 0",

        "& .MuiListItem-root": {
          padding: "16px 20px",

          "& .MuiListItemText-root": {
            display: "flex",
            flexDirection: "column",
            gap: "6px",
          },
        },
      },
    },
  },

  "mobile-detail": {
    backgroundColor: "#ffffff",
    display: "flex",
    flex: "1 1 100%",

    "& .wrapper": {
      display: "flex",
      flex: "1 1 0",
      flexDirection: "column",

      "& .head": {
        boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.1)",
        display: "flex",
        justifyContent: "space-between",
        padding: "12px 20px",

        "& .left": {
          display: "flex",
          flexDirection: "column",
          flexGrow: 1,
          gap: "10px",

          "& .title": {
            color: "#24272C",
            fontSize: "18px",
            fontWeight: 500,
            lineHeight: "21px",
          },
        },

        "& .right": {
          flexShrink: 1,

          "& .badge": {
            height: "16px",

            "& .MuiChip-label": {
              color: "#FFFFFF",
              fontSize: "10px",
              fontWeight: 900,
              lineHeight: "12px",
            },
          },
        },
      },

      "& .content": {
        flex: "1 1 0",
        paddingBottom: "56px",

        "& .detail-grid": {
          padding: "20px",

          "& .grid-head": {
            display: "flex",
            justifyContent: "space-between",
            marginBottom: "16px",

            "& .title": {
              color: "#24272C",
              fontSize: "15px",
              fontWeight: 700,
              lineHeight: "40px",
            },

            "& .download": {
              alignItems: "baseline",
              color: "#12B0EE",
              display: "flex",
              fontSize: "14px",
              fontWeight: 500,
              gap: "8px",
              lineHeight: "40px",
            },
          },

          "& .inner": {
            display: "flex",
            flexWrap: "wrap",
            gap: "30px 15px",

            "& .report-item": {
              background: "#FFFFFF",
              boxShadow: "0px 0px 20px rgba(50, 197, 255, 0.15)",
              borderRadius: "8px",
              display: "flex",
              flex: "1 1 calc((100% / 3) - (15px * 2))",
              flexDirection: "column",
              gap: "30px 0",
              padding: "7px",
              minWidth: "138px",

              "&:hover": {
                boxShadow:
                  "0px 0px 20px 5px rgba(50, 197, 255, 0.15), inset 0 0 0 1px #12B0EE",
              },

              "& .top": {
                display: "flex",
                justifyContent: "space-between",

                "& .item-title": {
                  color: "#24272C",
                  fontSize: "16px",
                  fontWeight: 500,
                  lineHeight: "14px",
                },
              },

              "& .bottom": {
                "& .item-value": {
                  color: "#24272C",
                  fontSize: "20px",
                  fontWeight: 700,
                  lineHeight: "23px",
                },

                "& .item-unit": {
                  color: "#24272C80",
                  fontSize: "12px",
                  fontWeight: 500,
                  lineHeight: "14px",
                },
              },
            },
          },
        },

        "& .linked-template, & .linked-users, & .schedule, & .users, & .api": {
          padding: "20px",

          "& .title": {
            color: "#24272C",
            fontSize: "15px",
            fontWeight: 700,
            lineHeight: "40px",
          },

          "& .MuiLink-root": {
            alignItems: "center",
            color: "#12B0EE",
            display: "flex",
            fontSize: "12px",
            fontWeight: 400,
            lineHeight: "12px",
            columnGap: "7px",
          },

          "& .template": {
            alignItems: "baseline",
            display: "flex",
            gap: "0 10px",
          },

          "& .MuiFilledInput-root": {
            backgroundColor: "#12B0EE1A",
            borderRadius: "8px",
            height: "40px",
            width: "100%",

            "&::before, &:after": {
              display: "none",
            },

            "& *::-moz-selection, & *::selection": {
              backgroundColor: "transparent",
              color: "#24272C80",
            },

            "& .MuiFilledInput-input": {
              color: "#24272C80",
              fontSize: "12px",
              fontWeight: 500,
              height: "100%",
              lineHeight: "14px",
              padding: "0 12px",
            },
          },

          "& .name, & .at": {
            color: "#767676B3",
            fontSize: "14px",
            fontWeight: 400,
            lineHeight: "15px",
          },
        },

        "& .actions": {
          columnGap: "15px",
          display: "flex",
          justifyContent: "flex-end",
          padding: "20px",

          "& .MuiButton-root": {
            alignItems: "center",
            borderRadius: "8px",
            boxShadow: "none",
            fontSize: "15px",
            fontWeight: 500,
            height: "43px",
            lineHeight: "18px",
            textTransform: "capitalize",

            "& .MuiLoadingButton-loadingIndicator": {
              color: "#ffffff",
            },

            "&.MuiButton-containedPrimary": {
              backgroundColor: "#12B0EE",
            },

            "&.MuiButton-containedSecondary": {
              backgroundColor: "#BEBEBE",

              "&.btn-resume": {
                backgroundColor: "#47C055",
              },
            },
          },
        },
      },
    },
  },

  "new-campaign": {
    "& .MuiBackdrop-root": {
      backgroundColor: "#00000080",
    },

    "& .wrapper": {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      width: "360px",

      "& > .header": {
        alignItems: "center",
        display: "flex",
        flex: "0 1 0",
        gap: "10px",
        padding: "36px 30px",
        width: "100%",

        "& .title": {
          color: "#24272c",
          flexGrow: 1,
          fontWeight: 500,
          fontSize: "24px",
          lineHeight: "28px",
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
        },
      },

      "& > form": {
        flex: "1 0 0",
        overflowY: "auto",
        padding: "0 40px",

        "& .form-title": {
          color: "#000000",
          fontSize: "16px",
          fontWeight: 500,
          lineHeight: "19px",
          marginBottom: "24px",
        },

        "& .field": {
          marginBottom: "16px",

          "&.flex": {
            display: "flex",
            justifyContent: "space-between",
            gap: "20px",
          },

          "& .field-title": {
            color: "#24272C",
            fontSize: "16px",
            fontWeight: 500,
            lineHeight: "19px",
          },

          "& .sample": {
            alignItems: "center",
            display: "flex",
            justifyContent: "center",
            margin: "33px 0",
            textAlign: "center",

            "& .MuiLink-root": {
              color: "#12B0EE",
              display: "flex",
              fontSize: "16px",
              fontWeight: 500,
              gap: "16px",
              lineHeight: "19px",
            },
          },

          "& .MuiInputBase-root": {
            "&::before, &::after": {
              display: "none",
            },
          },

          "& .MuiInputBase-input": {
            color: "#24272C",
            fontSize: "15px",
            fontWeight: 400,
            lineHeight: "18px",
          },

          "& .MuiInput-root": {
            padding: "16px 14px",

            "&:not(.MuiInput-multiline)": {
              height: "52px",

              "&::before, &::after": {
                display: "none",
              },
            },
          },

          "& .MuiFormControl-root": {
            height: "52px",

            "& .MuiInputLabel-filled": {
              transform: "translate(14px, 20px) scale(1)",

              "&.MuiInputLabel-shrink": {
                transform: "translate(14px, 10px) scale(0.75)",
              },
            },
          },

          "& .MuiSelect-select": {
            paddingLeft: "14px",

            "&:focus": {
              backgroundColor: "transparent",
            },

            "& .MuiSelect-iconFilled": {
              color: "#24272C",
              right: "17px",
            },
          },
        },

        "& .actions": {
          display: "flex",
          justifyContent: "flex-end",
          gap: "16px",
          padding: "20px 0",

          "& .MuiButton-root": {
            backgroundColor: "#12B0EE",
            borderRadius: "8px",
            boxShadow: "none",
            height: "40px",
            width: "130px",
          },
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {
    container: {
      backgroundColor: "#FFFFFF",
      borderRadius: "8px",
      flexGrow: 1,
      height: "calc(100% - (10px * 4))",
      margin: "auto 10px auto 20px",
      overflow: "hidden",

      "& .inner-wrapper": {
        display: "flex",
        height: "100%",
        position: "relative",

        "& .list": {
          borderRight: "1px solid rgba(0, 0, 0, 0.05)",
          display: "flex",
          flexDirection: "column",
          flex: "0 1 100%",
          width: "100%",

          "& .head": {
            alignItems: "center",
            display: "flex",
            padding: "10px 16px",
            position: "relative",

            "& .title-area": {
              alignItems: "baseline",
              display: "flex",
              gap: "5px",

              "& .title": {
                color: "#24272C",
                fontSize: "20px",
                fontWeight: 700,
                lineHeight: "23px",
              },

              "& .add-new": {
                color: "#12B0EE",
                fontSize: "12px",
                fontWeight: 500,
                lineHeight: "14px",
              },
            },

            "& .search": {
              display: "flex",
              flexGrow: 1,

              "& .search-input": {
                border: "1px solid #24272C80",
                borderRadius: "8px",
                flexGrow: 1,
                margin: 0,
                padding: "0 12px",
                outline: "none",
              },
            },

            "& .MuiIconButton-root": {
              backgroundColor: "transparent",
              padding: "4px",
              maxHeight: "32px",

              "&.btn-search": {
                marginLeft: "auto",
              },

              "&:hover": {
                backgroundColor: "transparent",
              },
            },
          },

          "& .campaign": {
            display: "flex",
            flex: "1 0 0",
            flexDirection: "column",

            "& .MuiTabs-root": {
              padding: "0 16px",

              "& .MuiTab-root": {
                marginRight: "4px",
                padding: "10px",
              },
            },

            "& .MuiList-root": {
              flex: "1 0 0",
              overflow: "auto",

              "& .MuiListItem-root": {
                padding: "16px",

                "& .MuiListItemText-root": {
                  display: "flex",
                  flexDirection: "column",
                  gap: "6px",
                },
              },
            },
          },
        },

        "& .detail": {
          backgroundColor: "#ffffff",
          display: "flex",
          flex: "1 1 100%",
          height: "100%",
          position: "absolute",
          left: 0,
          top: 0,

          "& .wrapper": {
            display: "flex",
            flex: "1 1 0",
            flexDirection: "column",

            "& .head": {
              borderBottom: "1px solid rgba(0, 0, 0, 0.05)",
              display: "flex",
              justifyContent: "space-between",
              padding: "12px 20px",

              "& .left": {
                display: "flex",
                flexDirection: "column",
                flexGrow: 1,
                gap: "10px",

                "& .title": {
                  color: "#24272C",
                  fontSize: "18px",
                  fontWeight: 500,
                  lineHeight: "21px",
                },
              },

              "& .right": {
                flexShrink: 1,

                "& .badge": {
                  height: "16px",

                  "& .MuiChip-label": {
                    color: "#FFFFFF",
                    fontSize: "10px",
                    fontWeight: 900,
                    lineHeight: "12px",
                  },
                },
              },
            },

            "& .content": {
              flex: "1 1 0",
              overflow: "auto",

              "& .detail-grid": {
                padding: "20px",

                "& .grid-head": {
                  display: "flex",
                  justifyContent: "space-between",
                  marginBottom: "16px",

                  "& .title": {
                    color: "#24272C",
                    fontSize: "15px",
                    fontWeight: 700,
                    lineHeight: "40px",
                  },

                  "& .download": {
                    alignItems: "baseline",
                    color: "#12B0EE",
                    display: "flex",
                    fontSize: "14px",
                    fontWeight: 500,
                    gap: "8px",
                    lineHeight: "40px",
                  },
                },

                "& .inner": {
                  display: "flex",
                  flexWrap: "wrap",
                  gap: "30px 15px",

                  "& .report-item": {
                    background: "#FFFFFF",
                    boxShadow: "0px 0px 20px rgba(50, 197, 255, 0.15)",
                    borderRadius: "8px",
                    display: "flex",
                    flex: "1 1 calc((100% / 3) - (15px * 2))",
                    flexDirection: "column",
                    gap: "30px 0",
                    padding: "7px",
                    minWidth: "138px",

                    "&:hover": {
                      boxShadow:
                        "0px 0px 20px 5px rgba(50, 197, 255, 0.15), inset 0 0 0 1px #12B0EE",
                    },

                    "& .top": {
                      display: "flex",
                      justifyContent: "space-between",

                      "& .item-title": {
                        color: "#24272C",
                        fontSize: "16px",
                        fontWeight: 500,
                        lineHeight: "14px",
                      },
                    },

                    "& .bottom": {
                      "& .item-value": {
                        color: "#24272C",
                        fontSize: "20px",
                        fontWeight: 700,
                        lineHeight: "23px",
                      },

                      "& .item-unit": {
                        color: "#24272C80",
                        fontSize: "12px",
                        fontWeight: 500,
                        lineHeight: "14px",
                      },
                    },
                  },
                },
              },

              "& .linked-template, & .linked-users, & .schedule, & .users, & .api":
                {
                  padding: "20px",

                  "& .title": {
                    color: "#24272C",
                    fontSize: "15px",
                    fontWeight: 700,
                    lineHeight: "40px",
                  },

                  "& .MuiLink-root": {
                    alignItems: "center",
                    color: "#12B0EE",
                    display: "flex",
                    fontSize: "12px",
                    fontWeight: 400,
                    lineHeight: "12px",
                    columnGap: "7px",
                  },

                  "& .MuiFilledInput-root": {
                    backgroundColor: "#12B0EE1A",
                    borderRadius: "8px",
                    height: "40px",
                    width: "100%",

                    "&::before, &:after": {
                      display: "none",
                    },

                    "& *::-moz-selection, & *::selection": {
                      backgroundColor: "transparent",
                      color: "#24272C80",
                    },

                    "& .MuiFilledInput-input": {
                      color: "#24272C80",
                      fontSize: "12px",
                      fontWeight: 500,
                      height: "100%",
                      lineHeight: "14px",
                      padding: "0 12px",
                    },
                  },

                  "& .template": {
                    alignItems: "baseline",
                    display: "flex",
                    gap: "0 10px",
                  },

                  "& .name, & .at": {
                    color: "#767676B3",
                    fontSize: "14px",
                    fontWeight: 400,
                    lineHeight: "15px",
                  },
                },

              "& .actions": {
                columnGap: "15px",
                display: "flex",
                justifyContent: "flex-end",
                padding: "20px",

                "& .MuiButton-root": {
                  alignItems: "center",
                  borderRadius: "8px",
                  boxShadow: "none",
                  fontSize: "15px",
                  fontWeight: 500,
                  height: "43px",
                  lineHeight: "18px",
                  textTransform: "capitalize",

                  "& .MuiLoadingButton-loadingIndicator": {
                    color: "#ffffff",
                  },

                  "&.MuiButton-containedPrimary": {
                    backgroundColor: "#12B0EE",
                  },

                  "&.MuiButton-containedSecondary": {
                    backgroundColor: "#BEBEBE",

                    "&.btn-resume": {
                      backgroundColor: "#47C055",
                    },
                  },
                },
              },
            },
          },

          "& .preview": {
            borderLeft: "1px solid rgba(0, 0, 0, 0.05)",
            display: "none",
            flexDirection: "column",
            flex: "0 1 auto",
            height: "100%",
            width: "100%",
            maxWidth: "300px",

            "& .header": {
              padding: "12px 20px",

              "& .heading": {
                color: "#24272C",
                fontSize: "18px",
                fontWeight: 500,
                lineHeight: "21px",
              },
            },

            "& .preview-content": {
              flex: "1 1 0",
              padding: "30px 20px",

              "& .preview-card": {
                backgroundColor: "#FFFFFF",
                border: "1px solid #DEE2EA",
                boxShadow: "0 0 20px rgba(0, 0, 0, 0.1)",
                borderRadius: "4px",
                margin: "0 auto 20px",
                maxWidth: "300px",
                padding: "15px",
                height: "max-content",

                "& .media-wrapper": {
                  alignItems: "center",
                  backgroundColor: "#C4C4C4",
                  borderRadius: "4px 4px 0 0",
                  display: "flex",
                  justifyContent: "center",
                  marginBottom: "24px",
                  minHeight: "130px",
                  overflow: "hidden",
                  position: "relative",
                  width: "100%",

                  "&.doc": {
                    backgroundColor: "#F5F5F5",
                    color: "#A1AEB4",
                    fontSize: "90px",
                    fontWeight: 900,
                    lineHeight: "96px",
                  },

                  "&.vdo": {
                    "&::after": {
                      borderLeft: "32px solid #ffffff",
                      borderTop: "16px solid transparent",
                      borderBottom: "16px solid transparent",
                      content: '""',
                      cursor: "pointer",
                      display: "block",
                      filter: "drop-shadow(0px 5px 2px #00000040)",
                      left: "50%",
                      top: "50%",
                      transform: "translate(-50%,-50%)",
                      position: "absolute",
                    },

                    "&.playing": {
                      "&:hover": {
                        "&::after": {
                          display: "block",
                        },
                      },

                      "&::after": {
                        borderTop: "none",
                        borderBottom: "none",
                        borderLeft: "12px solid #ffffff",
                        borderRight: "12px solid #ffffff",
                        content: '""',
                        cursor: "pointer",
                        display: "none",
                        height: "32px",
                        left: "50%",
                        top: "50%",
                        transform: "translate(-50%,-50%)",
                        position: "absolute",
                        width: "8px",
                      },
                    },
                  },

                  "& .image, & .video": {
                    objectFit: "cover",
                    objectPosition: "center",
                    width: "100%",
                  },

                  "& .video": {
                    height: "130px",
                  },
                },

                "& .header": {
                  color: "#767676",
                  fontSize: "16px",
                  fontWeight: 700,
                  lineHeight: "16px",
                  marginBottom: "5px",
                  minHeight: "unset!important",
                  padding: "0!important",
                },

                "& .body": {
                  color: "#767676",
                  fontSize: "14px",
                  fontWeight: 400,
                  lineHeight: "16px",
                  marginBottom: "12px",

                  "& >": {
                    "& *": {
                      "&:not(:last-child)": {
                        marginBottom: "16px",
                      },
                    },
                  },
                },

                "& .footer": {
                  alignItems: "flex-start!important",
                  border: "none!important",
                  color: "#76767680",
                  fontSize: "12px",
                  fontWeight: 500,
                  lineHeight: "16px",
                  marginBottom: "20px",
                  minHeight: "unset!important",
                  padding: "0!important",
                },

                "& .preview-actions": {
                  display: "flex",
                  flexWrap: "wrap",
                  justifyContent: "space-between",
                  gap: "16px",

                  "& .btn-action": {
                    backgroundColor: "#ffffff",
                    border: "1px solid #DEE2EA",
                    borderRadius: "8px",
                    color: "#767676",
                    flex: "1 1 auto",
                    fontSize: "14px",
                    fontWeight: 500,
                    height: "40px",
                    lineHeight: "16px",
                    padding: "0 5px",
                    textTransform: "initial",
                    minWidth: "calc(50% - 8px)",

                    "& .label": {
                      overflow: "hidden",
                      textAlign: "left",
                      textOverflow: "ellipsis",
                      whiteSpace: "nowrap",
                    },
                  },
                },
              },
            },
          },
        },

        "& .create-or-new": {
          backgroundColor: "#F4F6F9",
          boxShadow: "56px 4px 19px -49px #00000017 inset",
          display: "flex",
          flex: "1 0 0",
          flexDirection: "column",
          justifyContent: "space-between",

          "& .top": {
            alignItems: "center",
            display: "inline-flex",
            flexDirection: "column",
            marginTop: "80px",
            rowGap: "5px",
            textAlign: "center",

            "& .info": {
              "& .heading": {
                color: "#24272C",
                fontSize: "20px",
                fontWeight: 500,
                lineHeight: "42px",
              },
            },

            "& .MuiButton-root": {
              backgroundColor: "#12B0EE",
              borderRadius: "8px",
              boxShadow: "none",
              height: "42px",
            },
          },

          "& .bottom": {
            display: "flex",
            justifyContent: "flex-end",

            "& .image": {
              marginRight: "36px",
              marginBottom: "20px",
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("md")]: {
    container: {
      "& .inner-wrapper": {
        "& .list": {
          maxWidth: "25%",
          minWidth: "351px",
        },

        "& .detail": {
          "& .preview": {
            display: "flex",
          },
        },
      },
    },
  },
  [theme.breakpoints.up("lg")]: {
    container: {
      "& .inner-wrapper": {
        "& .detail": {
          position: "initial",
        },
      },
    },
  },
}));

export default useStyles;
