import React, { Fragment, useEffect, useRef, useState } from "react";
import {
  Button,
  Chip,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Link,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  Menu,
  MenuItem,
  styled,
  alpha,
} from "@mui/material";
import moment from "moment";

import { IconButton } from "@material-ui/core";
import { Search, Clear, MoreVert, ArrowDropDown } from "@material-ui/icons";
import { motion } from "framer-motion";

import onGoingImg from "../../../assets/campaign/ongoingIcon.png";
import linkImg from "../../../assets/campaign/link-icon.png";
import {
  getAllCampaignsByBusiness,
  updateCampaignProgress,
} from "../../../api/Campaign/Campaign";
import { useToastMessage } from "../../../contexts";
import { useCampaign } from "../../../contexts/CampaignProvider";
import { DotLoader } from "../../../Components/exports";

import NewCampaign from "../New";

import useStyles from "../style/muiStyles";

const CampaignMobileList = () => {
  const classes = useStyles();
  const [isActive, setActive] = useState(false);
  const [reload, doReload] = useState(false);
  const inputField = useRef(null);
  const { createNew, toggleCreateNew, setSearchQuery } = useCampaign();
  const [currentCampaign, setCurrentCampaign] = useState("all");
  const [loader, setLoader] = useState(false);
  const [campaignsData, setCampaignsData] = useState([]);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [anchorElOpt, setAnchorElOpt] = React.useState(null);
  const { searchQuery } = useCampaign();
  const openOptions = (event) => {
    setAnchorElOpt(event.currentTarget);
  };
  const closeOptions = () => {
    setAnchorElOpt(null);
  };
  const closeNew = () => {
    toggleCreateNew(false);
  };
  const handleChange = (newValue) => {
    setCurrentCampaign(newValue);
  };
  const handleSearch = (e) => {
    setSearchQuery(e.target.value);
  };
  //chip backGroundColor
  const getStatusColor = (status) => {
    if (status === "ACTIVE") {
      return "#FFBA43";
    }
    if (status === "PAUSED") {
      return "#EE9A9A";
    }
    if (status === "COMPLETED") {
      return "#12B347";
    }
  };
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const StyledMenu = styled((props) => (
    <Menu
      elevation={0}
      anchorOrigin={{
        vertical: "top",
        horizontal: "left",
      }}
      transformOrigin={{
        vertical: "top",
        horizontal: "left",
      }}
      {...props}
    />
  ))(({ theme }) => ({
    "& .MuiPaper-root": {
      borderRadius: 6,
      marginTop: theme.spacing(1),
      color:
        theme.palette.mode === "light"
          ? "rgb(55, 65, 81)"
          : theme.palette.grey[300],
      boxShadow:
        "rgb(255, 255, 255) 0px 0px 0px 0px, rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px",

      "& .MuiMenu-list": {
        padding: "4px 0",
      },

      "& .MuiMenuItem-root": {
        "& .MuiSvgIcon-root": {
          fontSize: 18,
          color: theme.palette.text.secondary,
          marginRight: theme.spacing(1.5),
        },

        "&:active": {
          backgroundColor: alpha(
            theme.palette.primary.main,
            theme.palette.action.selectedOpacity
          ),
        },
      },
    },
  }));
  const CampaignList = styled(List)({
    padding: 0,

    "& .no-results": {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      marginTop: "55%",
      position: "absolute",
      marginLeft: "40%",
    },

    "& .loader": {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      marginTop: "55%",
      position: "absolute",
      marginLeft: "40%",
    },
  });
  const CampaignListItem = styled(ListItem)({
    padding: "16px 20px",
    margin: 0,

    "&:not(:last-child)": {
      borderBottom: "1px solid #24272C1a",
    },

    "&.Mui-selected, &.Mui-selected:hover": {
      backgroundColor: "#F5FCFF",
    },

    "&:hover": {
      backgroundColor: "#F6F6F6",
    },
  });
  const CampaignListItemText = styled(ListItemText)({
    color: "#24272C",
    margin: 0,
    marginRight: "14px",

    "& .primary-box": {
      alignItems: "baseline",
      display: "flex",
      justifyContent: "space-between",

      "& .title": {
        color: "#24272C",
        fontSize: "15px",
        fontWeight: 5001,
        lineHeight: "18px",
      },

      "& .StatusChip": {
        height: "16px",

        "& .MuiChip-label": {
          color: "#FFFFFF",
          fontSize: "10px",
          fontWeight: 900,
          lineHeight: "12px",
        },
      },
    },

    "& .secondary-box": {
      alignItems: "baseline",
      display: "flex",
      gap: "5px",

      "& .secondary-text": {
        color: "#24272CB3",
        fontSize: "12px",
        fontWeight: 400,
        lineHeight: "14px",
      },
    },
  });

  const CampaignFragment = ({ campaign, updateCampaignsData }) => {
    const campaignId = campaign.id;
    const [anchorEl, setAnchorEl] = useState(null);
    const [openConfirmDialog, setOpenConfirmDialog] = useState(false);
    const {
      selectedCampaignId,
      setSelectedCampaignId,
      setLoad,
      setSelectedCampaignProgress,
    } = useCampaign();
    const handleClickOpen = () => {
      setOpenConfirmDialog(true);
    };
    const handleClickClose = () => {
      setOpenConfirmDialog(false);
    };
    const handleClick = (event) => {
      setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
      setAnchorEl(null);
    };
    const { setSuccessMessage, setErrorMessage } = useToastMessage();

    return (
      <Fragment>
        <CampaignListItem selected={campaign.id === selectedCampaignId}>
          <ListItemButton
            component="button"
            onClick={() => {
              if (campaign) {
                if (campaign.id === selectedCampaignId) {
                  return;
                }
                setLoad(true);
                setSelectedCampaignId(campaign.id);
                setSelectedCampaignProgress(campaign.progress);
              }
            }}
            disableRipple
            sx={{
              backgroundColor: "transparent",
              padding: 0,
              position: "initial",

              "&::after": {
                content: "''",
                position: "absolute",
                left: 0,
                top: 0,
                width: "100%",
                height: "100%",
              },

              "&:hover": {
                backgroundColor: "transparent",
              },
            }}
          >
            <CampaignListItemText
              primary={
                <div className="primary-box">
                  <h2 className="title">{campaign?.name}</h2>
                  <Chip
                    label={campaign?.progress}
                    className={"StatusChip"}
                    size="small"
                    style={{
                      backgroundColor: getStatusColor(campaign?.progress),
                    }}
                  />
                </div>
              }
              secondary={
                <span className="secondary-box">
                  <img
                    src={campaign?.type === "API" ? linkImg : onGoingImg}
                    alt={campaign?.type === "API" ? "api" : "ongoing"}
                  />
                  <span className={"secondary-text"}>
                    {campaign?.type === "API"
                      ? campaign?.type
                      : moment(campaign?.schedule_date).format(
                          "Do MMM YY, h:mm A"
                        )}
                  </span>
                </span>
              }
            />
          </ListItemButton>
          <IconButton className="btn-options" onClick={handleClick}>
            <MoreVert />
          </IconButton>
          {Boolean(anchorEl) && (
            <Menu
              anchorEl={anchorEl}
              open={Boolean(anchorEl)}
              onClose={handleClose}
              keepMounted
              MenuListProps={{
                "aria-labelledby": "Options",
              }}
              anchorOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              transformOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              sx={{
                "& .MuiPaper-root": {
                  minWidth: "120px",
                },
              }}
            >
              <MenuItem
                onClick={() => {
                  handleClickOpen();
                  handleClose();
                }}
              >
                {"Delete"}
              </MenuItem>
            </Menu>
          )}
        </CampaignListItem>
        <Dialog
          open={openConfirmDialog}
          aria-labelledby="responsive-dialog-title"
        >
          <DialogTitle id="responsive-dialog-title">{"Delete"}</DialogTitle>
          <DialogContent>{"Are you sure you want to delete?"}</DialogContent>
          <DialogActions>
            <Button
              onClick={() => {
                handleClickClose();
              }}
            >
              {"Cancel"}
            </Button>
            <Button
              onClick={async () => {
                try {
                  let result = await updateCampaignProgress(
                    campaign.id,
                    "REMOVED"
                  );
                  if (result.message) {
                    setSelectedCampaignId("");
                    updateCampaignsData((prevCampaignsData) =>
                      prevCampaignsData.filter(
                        (campaign) => campaign.id !== campaignId
                      )
                    );
                    setSuccessMessage("Campaign Deleted Successfully");
                  }
                } catch (err) {
                  setErrorMessage(err.message);
                } finally {
                  handleClickClose();
                }
              }}
              autoFocus
            >
              {"Delete"}
            </Button>
          </DialogActions>
        </Dialog>
      </Fragment>
    );
  };

  useEffect(() => {
    setLoader(true);
    getAllCampaignsByBusiness()
      .then((data) => {
        setCampaignsData(data.payload);
        setLoader(false);
      })
      .catch((err) => {
        console.error(err);
      });
  }, [reload]);

  return (
    <Fragment>
      <div className={classes["mobile-list"]}>
        <div className="head">
          {!isActive && (
            <div className="title-area">
              <h2 className="title">{"Campaigns"}</h2>
              <Link
                component="button"
                className="filter"
                underline="none"
                onClick={handleClick}
              >
                <span className="label">{currentCampaign}</span>
                <ArrowDropDown />
              </Link>
              <StyledMenu
                anchorEl={anchorEl}
                open={Boolean(anchorEl)}
                onClose={handleClose}
              >
                {["all", "active", "paused", "completed"].map(
                  (campaign, index) => (
                    <MenuItem
                      key={index}
                      onClick={() => {
                        handleChange(campaign);
                        handleClose();
                      }}
                      selected={currentCampaign === campaign}
                      disableRipple
                    >
                      {`${campaign.charAt(0).toUpperCase()}${campaign.slice(
                        1
                      )}`}
                    </MenuItem>
                  )
                )}
              </StyledMenu>
            </div>
          )}
          {isActive && (
            <div className="search">
              <IconButton
                onClick={() => {
                  setActive(true);
                }}
              >
                <Search />
              </IconButton>
              {
                <motion.input
                  onChange={handleSearch}
                  ref={inputField}
                  autoFocus={isActive}
                  placeholder="Search Campaign"
                  className="search-input"
                  animate={isActive ? "open" : "closed"}
                  variants={{
                    open: { opacity: 1, pointerEvents: "all" },
                    closed: { opacity: 0, pointerEvents: "none" },
                  }}
                  initial={{ opacity: 0, pointerEvents: "none" }}
                />
              }
              <IconButton
                onClick={() => {
                  setSearchQuery("");
                  setActive(!isActive);
                }}
              >
                <Clear />
              </IconButton>
            </div>
          )}
          {!isActive && (
            <IconButton
              className="btn-search"
              onClick={() => {
                setActive(true);
              }}
            >
              <Search />
            </IconButton>
          )}
          <IconButton className="btn-options" onClick={openOptions}>
            <MoreVert />
          </IconButton>
          <StyledMenu
            anchorEl={anchorElOpt}
            open={Boolean(anchorElOpt)}
            onClose={closeOptions}
          >
            <MenuItem
              onClick={() => {
                toggleCreateNew(true);
                closeOptions();
              }}
            >
              {"Add New"}
            </MenuItem>
          </StyledMenu>
        </div>
        <div className="campaign">
          <CampaignList>
            {!campaignsData && (
              <div className="no-results">
                <span>{"No results"}</span>
              </div>
            )}
            {loader && (
              <div className="loader">
                <DotLoader />
              </div>
            )}
            {campaignsData &&
              campaignsData
                .filter((campaign) =>
                  currentCampaign === "all"
                    ? true
                    : currentCampaign === campaign?.progress.toLowerCase()
                )
                .filter((element) =>
                  element.name.toLowerCase().includes(searchQuery.toLowerCase())
                )
                .reverse()
                .map((campaign) => (
                  <CampaignFragment
                    key={campaign.id}
                    campaign={campaign}
                    updateCampaignsData={setCampaignsData}
                  />
                ))}
          </CampaignList>
        </div>
      </div>
      <NewCampaign open={createNew} onClose={closeNew} doReload={doReload} />
    </Fragment>
  );
};

export default CampaignMobileList;
